.cartPage{
    padding: 5vmax;
}

.cartHeader{
    background-color:#7286d3;
    width: 90%;
    box-sizing: border-box;
    margin: auto;
    color: white;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 300 0.7vmax "Roboto";
}


.cartHeader > p{
    margin: 10px;
}

.cartHeader > p:last-child{
    text-align: end;
}


.cartContainer{
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
}

.cartInput{
    display: flex;
    align-items: center;
    height: 8vmax;
}


.cartInput > button{
    border: none;
    background-color: rgba(0, 0, 0, 0.616);
    cursor: pointer;
    color: white;
    transition: aall 0.5s;
    padding: 0.5vmax;
}

.cartInput > button:hover{
    background-color: rgba(0, 0, 0, 0.767);
}


.cartInput > input{
    border: none;
    padding: 0.5vmax;
    width: 1vmax;
    font: 400 0.8vmax 'Roboto';
    color: rgba(0, 0, 0, 0.74);
    outline: none;
    text-align: center;
}


.cartSubtotal{
    display: flex;
    padding: 0.5vmax;
    justify-content: flex-end;
    color: rgba(0, 0, 0, 0.753);
    box-sizing: border-box;
    align-items: center;
    height: 8vmax;
}



.cartGrossProfit{
    display: grid;
    grid-template-columns: 2fr 1.2fr;
}


.cartGrossProfitBox{
    border-top: 3px solid #7286d3;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    padding: 2vmax 0;
    font: 300 1vmax 'Roboto';
    display: flex;
    justify-content: space-between;
}


.checkoutBtn{
    display: flex;
    justify-content: flex-end;
}


.checkoutBtn > button{
    background-color:#7286d3;
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 0.8vmax 'Roboto';
    margin: 1vmax 4vmax;
    cursor: pointer;
    border-radius: 30px;
}



.emptyCart{
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    padding: 10vmax;
}


.emptyCart > svg{
    font-size: 5vmax;
    color: #7286d3
}

.emptyCart > p{
    font-size: 2vmax;
}

.emptyCart >a {
    background-color: rgb(51, 51, 51);
    color: white;
    font: 400 1vmax 'Roboto';
    text-decoration: none;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
}


@media screen and (max-width : 600px) {
    .cartPage{
        padding: 0;
        min-width: 60vh
    }
.cartHeader{
    width: 100%;
    font: 300 1.7vmax 'Roboto';
    grid-template-columns: 3fr 1fr 1fr;
}

.cartContainer{
    width: 100%;
    grid-template-columns: 3fr 3fr 3fr;
}

.cartInput{
    height: 20vmax;
}

.cartInput >button{
    padding: 1.5vmax;
}

.cartInput > input{
    padding: 1.5vmax;
    width: 2vmax;
    font: 400 1.8vmax 'Roboto';
}
.cartSubtotal{
    padding: 1.5vmax;
    height: 20vmax;
    font: 300 2vmax 'Roboto';
}

.cartGrossProfit{
    display: grid;
    grid-template-columns: 0fr 2fr;
}
.cartGrossProfitBox{
    padding: 2vmax;
    font: 300 2vmax 'Roboto';
}


.checkoutBtn > button{
    padding: 2vmax 4vmax;
    width: 100%;
    font: 300 2vmax 'Roboto';
}

}